import axios from "axios";
const datos_personales={
    namespaced:true,
    state:{
        prueba:[],
        pruebaBasica:[],
        pruebaSecundaria:[],
        pruebaOtros:[],
        pruebaContacto:[],
        pruebasEmpleos:[],
        pruebaProyectos:[]
    },
    getters:{},
    mutations:{
        CONSULTA_DATOS_PERSONALES:(state, unaPrueba)=>{
            state.prueba= unaPrueba;
        },
        CONSULTA_DATOS_ESTUDIOS:(state, otraPrueba)=>{
            state.pruebaBasica= otraPrueba;
        },
        CONSULTA_DATOS_ESTUDIOS_MEDIA:(state, pruebaTres)=>{
            state.pruebaSecundaria=pruebaTres;
        },
        CONSULTA_DATOS_OTROS:(state, datosOtros)=>{
            state.pruebaOtros=datosOtros;
        },
        CONSULTA_DATOS_CONTACTO:(state, datosContacto)=>{
            state.pruebaContacto=datosContacto;
        },
        CONSULTA_EMPLEOS:(state, datosEmpleos)=>{
            state.pruebasEmpleos=datosEmpleos;
        },
        CONSULTA_PROYECTOS:(state, datosProyectos)=>{
            state.pruebaProyectos=datosProyectos;
        }
        
    },
    actions:{
        consultarDatosPersonales:(context)=>{
            let urlDatosPersonales ='datos.json';
            
            axios.get(urlDatosPersonales)
                .then(respuesta=>{  
                    context.commit('CONSULTA_DATOS_PERSONALES', respuesta["data"]["datosP"]);
                })
                .catch(err=>{
                    console.log(err);
                });
        },
        consultarDatosEstudios:(context)=>{
            let urlDatosEstudios ='datos.json';
            
            axios.get(urlDatosEstudios)
                .then(respuesta=>{
                    context.commit('CONSULTA_DATOS_ESTUDIOS', respuesta["data"]["estudiosPrimario"]);
                })
                .catch(err=>{
                    console.log(err);
                });
        },
        consultarDatosEstudiosMedia:(context)=>{
            let urlDatosEstudiosMedia ='datos.json';
            
            axios.get(urlDatosEstudiosMedia)
                .then(respuesta=>{
                    console.log(respuesta)
                    context.commit('CONSULTA_DATOS_ESTUDIOS_MEDIA', respuesta["data"]["estudioSecundario"]);
                })
                .catch(err=>{
                    console.log(err);
                });
        },
        consultarDatoOtros:(context)=>{
            let urlDatosEstudiosOtros='datos.json';
            
            axios.get(urlDatosEstudiosOtros)
                .then(respuesta=>{
                    console.log(respuesta)
                    context.commit('CONSULTA_DATOS_OTROS', respuesta["data"]["bootCamps"]);
                })
                .catch(err=>{
                    console.log(err);
                });
        },
        consultarEnlaces:(context)=>{
            let urlDatosContacto='datos.json';
            
            axios.get(urlDatosContacto)
                .then(respuesta=>{
                    console.log(respuesta)
                    context.commit('CONSULTA_DATOS_CONTACTO', respuesta["data"]["contacto"]);
                })
                .catch(err=>{
                    console.log(err);
                });
        },
        consultarEmpleos:(context)=>{
            let urlDatosEmpleos='datos.json';
            
            axios.get(urlDatosEmpleos)
                .then(respuesta=>{
                    console.log(respuesta)
                    context.commit('CONSULTA_EMPLEOS', respuesta["data"]["trabajos"]);
                })
                .catch(err=>{
                    console.log(err);
                });
        },
        consultarProyectos:(context)=>{
            let urlDatosProyectos='datos.json';
            
            axios.get(urlDatosProyectos)
                .then(respuesta=>{
                    console.log(respuesta)
                    context.commit('CONSULTA_PROYECTOS', respuesta["data"]["producciones"]);
                })
                .catch(err=>{
                    console.log(err);
                });
        },
    },
    modules:{}
}
export default datos_personales;